import { Component, EventEmitter, Output } from '@angular/core';
import { LoginPageModelClass } from '../login-model/LoginPageModel.class';
import { DomSanitizer } from '@angular/platform-browser';
import { LoginMessages } from '../../i18n/login-messages';

@Component({
  selector: 'ls-forgot-password-success',
  templateUrl: './forgot-password-success.component.html',
  styleUrls: ['./forgot-password-success.component.scss']
})
export class ForgotPasswordSuccessComponent extends LoginPageModelClass {

  @Output()
  backToLogin: EventEmitter<any> = new EventEmitter();

  loginMessages = LoginMessages;

  constructor(public _DomSanitizationService: DomSanitizer) {
    super(_DomSanitizationService);
  }

  backToLoginPage() {
    this.backToLogin.emit();
  }
}
