import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { Weight } from '../../ui-concepts/weight';
import { HasId } from '../../model/has-id';
import { ActionItemsSet } from '../../model/action-items-set';

@Component({
  selector: 'ls-action-bar',
  templateUrl: './action-bar.component.html',
  styleUrls: ['./action-bar.component.scss']
})
export class ActionBarComponent<T extends HasId> implements OnInit {

  @Input() primaryActions: ActionItemsSet<T> = {actionItems: []};
  @Input() secondaryActions: ActionItemsSet<T> = {actionItems: []};
  @Input() otherActions: ActionItemsSet<T> = {actionItems: []};
  @Input() argument: T;
  @Output() needReload = new EventEmitter();
  @Output() actionResult = new EventEmitter();

  weight = Weight;

  constructor() {
  }

  ngOnInit(): void {
    this.secondaryActions.actionItems
      .forEach(item => item.initializeItem());
    this.primaryActions.actionItems
      .forEach(item => item.initializeItem());
    this.otherActions.actionItems
      .forEach(item => item.initializeItem());
  }

  sendResult($event: any) {
    this.actionResult.emit($event);
  }
}
